S,CAAA,qBAGX,aACE,CAAA,4BAGS,gBACT,CAAA,mEH5DA,wBdIkB,CAAA,qCcDY,mEAH9B,kBAII,CAAA,CAAA,qCAG0B,mEAP9B,eAQI,CAAA,wBdFiB,CAAA,CAAA,sDcOrB,oBACW,CAAA,+DAET,gCACc,CAAA,KAAA,sCxBdiB,CAAA,qB4BgCnC,CAAA,aACK,C/BKM,kB+BCb,YAGE,CAAA,EAAA,aAIK,C/BhDA,oB+BiDL","file":"active_admin.scss","sourcesContent":["*, *:after, *:before {\n  -webkit-box-sizing: border-box;\n  -moz-box-sizing: border-box;\n  box-sizing: border-box;\n}\n\nhtml {\n  -webkit-tap-highlight-color: rgba(0,0,0,0);\n}\n\nhtml, body {\n  padding: 0;\n  margin: 0;\n  min-height: 100vh;\n}\n\nfieldset {\n  border: none;\n  margin: 0;\n  padding: 0;\n}\n\nlegend {\n  padding: 0;\n}\n\nol, ul {\n  margin: 0;\n  padding: 0;\n  list-style: none;\n}\n\nth {\n  font-weight: normal;\n}\n\nabbr[title] {\n  border-bottom: none;\n  cursor: inherit;\n  text-decoration: none;\n}\n","code {\n  display: block;\n  background-color: #272822;\n  padding: 5px;\n  color: white;\n}\n\nhr {\n  border: 1px solid $border-color;\n  color: $primary-color;\n  margin: 10px auto 25px auto;\n}","// BASIC COLOR\n$red: #B02B2C;\n$green: #00897B;\n$blue: #4bacfe;\n$black: #36393D;\n$grey: #76838f;\n\n// flash\n$error: #dc4747 !default;\n$warning: #f2a654 !default;\n$success: #46be8a !default;\n$info: #57c7d4 !default;\n$flash-text-color: #fff !default;\n$flash-background-color: $grey !default;\n\n// status_tag\n$status-tag-text-color: #fff !default;\n$status-tag-background-color: #cacaca !default;\n$status-tag-background-valid-color: $success !default;\n$status-tag-background-error-color: $error !default;\n\n$border-color: #e4eaec;\n\n\n// body\n$body-background: #eee !default;\n\n// header\n$header-border-color: #e6e6e6 !default;\n$header-background: #fff !default;\n\n// nav action\n$header-nav-action-hover-background: #f5f5f5 !default;\n$header-nav-action-active-background: #f0f0f0 !default;\n\n//sidebar\n$sidebar-border-color: $header-border-color !default;\n$sidebar-background: #f5f5f5 !default;\n\n$sidebar-item-hover-background: #f0f0f0 !default;\n$sidebar-nested-item-hover-background: #e7e7e7 !default;\n\n// text\n$text-color: #5a5a5a !default;\n$text-color-important: #526069 !default;\n\n$box-shadow: 0 0 4px 0 rgba(0,0,0,0.1) !default;\n\n$primary-color: $blue !default;","@mixin button() {\n  border-radius: $border-radius;\n  display: inline-block;\n  @include transition-button();\n  cursor: pointer;\n\n  &:active, &:focus {\n    outline: 0;\n  }\n\n  &.disabled, &:disabled {\n    opacity: 0.6;\n    cursor: no-drop;\n  }\n}\n\n@mixin primary-button($background-color, $color) {\n  background-color: $background-color;\n  color: $color;\n  border: none; \n  @include button();\n\n  &:hover {\n    background-color: lighten($background-color, 10%);\n  }\n\n  &:active, &:focus {\n    background-color: darken($background-color, 10%); \n  }\n\n  &:disabled {\n    background-color: $background-color;\n  }\n}\n\n@mixin secondary-button($color) {\n  background-color: #fff;\n  color: $color;\n  border: 1px solid $color;\n  @include button();\n\n  &:hover {\n    color: #fff;\n    background-color: $color;\n  }\n\n  &:active, &:focus {\n    background-color: darken($color, 10%);\n    color: #fff;\n  }\n}\n\n@mixin secondary-dropdown($color) {\n  @include secondary-button($color);\n  padding: 6px 25px 6px 10px;\n  position: relative;\n\n  &:after {\n    @include icon(\"\\f0dd\");\n    position: absolute;\n    right: 8px;\n  }\n\n  &:active, &:focus {\n    background-color: darken($color, 10%); \n    color: #fff;\n  }\n\n  &.disabled, &:disabled {\n    color: $grey;\n    background-color: #f3f7f9;\n    border-color: #f3f7f9;\n    @include box-shadow(none);\n    opacity: .65;\n  }\n}\n\n@mixin group-button($color) {\n  a {\n    display: inline-block;\n    color: $color;\n    border-top: 1px solid $color;\n    border-bottom: 1px solid $color;\n    float: left;\n    padding: 6px 10px;\n    border-right: 1px solid $color;\n    @include transition-button();\n  }\n\n  &:first-child a {\n    border-left: 1px solid $color;\n    border-top-left-radius: $border-radius;\n    border-bottom-left-radius: $border-radius;\n  }\n\n  &:last-child a {\n    border-top-right-radius: $border-radius;\n    border-bottom-right-radius: $border-radius;\n  }\n\n  &:hover {\n    a {\n      background-color: lighten($color, 10%);\n      border-color: lighten($color, 10%);\n      color: #fff;\n    }\n  }\n\n  &:active, &:focus, &.selected {\n    a {\n      background-color: darken($color, 10%);\n      border-color: darken($color, 10%);\n      color: #fff;\n    }\n  }\n}\n\n@mixin little-button() {\n  padding: 6px 12px;\n}",".button {\n  @include primary-button($primary-color, #fff);\n  padding: 5px 8px;\n  font-size: 16px;\n\n  &.small {\n    padding: 2px 9px;\n    font-size: 12px;\n  }\n\n  &.large {\n    padding: 10px;\n    font-size: 18px;\n  }\n\n  &.action {\n    margin-right: 4px;\n  }\n}\n","@import \"colors\";\n@import \"size\";\n@import \"media_queries\";\n\n$border-radius: 3px !default;\n$font-family-body: \"LatoWeb\", Arial, sans-serif !default;","@mixin transform($var) {\n  -webkit-transform: $var;\n     -moz-transform: $var;\n      -ms-transform: $var;\n       -o-transform: $var;\n          transform: $var;\n}\n\n@mixin appearance($effect:none) {\n  -webkit-appearance: $effect;\n     -moz-appearance: $effect;\n      -ms-appearance: $effect;\n       -o-appearance: $effect;\n          appearance: $effect;\n}\n\n@mixin transition($args...) {\n  -webkit-transition: $args;\n  -o-transition: $args;\n  transition: $args;\n}\n\n@mixin transition-button() {\n  @include transition(border .2s linear,color .2s linear,width .2s linear,background-color .2s linear);\n}\n\n@mixin box-shadow($args...) {\n  -webkit-box-shadow: $args;\n  -moz-box-shadow: $args;\n  box-shadow: $args;\n}\n\n@mixin disable-user-select() {\n  -webkit-touch-callout: none;\n  -webkit-user-select: none;\n  -khtml-user-select: none;\n  -moz-user-select: none;\n  -ms-user-select: none;\n  user-select: none;\n}\n\n@mixin clear-fix() {\n  &:a